import _get from 'lodash.get';
import { FieldPolicy , gql, FetchResult, ApolloCache } from '@apollo/client';

import { TListProperties, TProperty } from '../../types';
import { getProperty } from '../../graphql/queries';

export const listProperties: FieldPolicy = {
	keyArgs: false,
	merge<T extends TListProperties>(existing: T | undefined, incoming: T) {
		if (!existing) return incoming;

		return {
			items: [...existing.items, ...incoming.items],
			nextToken: incoming.nextToken,
		};
	},
};

export const updatePropertyCache =
	(id: string) => (cache: ApolloCache<any>, result: FetchResult<TProperty>) => {
		const updatedForm = _get(result, ['data', 'updateForm'], {});

		cache.writeQuery({
			query: gql(getProperty),
			data: {
				getProperty: updatedForm,
			},
			variables: { PropertyId: id },
		});
	};
